/* General */

:root {
	--navbar-gray-background-color: #55834f;
	--navbar-shadow-color: rgba(58, 54, 54, 0.753);
	--dropdown-background-color: #4f525f;
	--hover-dropdown-background-color: #757883;
	--font-white-color: #ffffff;
	--hover-font-white-color: #c9c9c9;
}

* {
	margin: 0;
	padding: 0;
	font-family: 'Play', sans-serif;
}

html {
	scroll-behavior: smooth;
}

html, body{
	height: 100%;
}
/* Header */

.toggle {
	width: 100%;
	padding: 10px 20px;
	background-color: #55834f;
	background-color: var(--navbar-gray-background-color);
	color: #ffffff;
	color: var(--font-white-color);
	text-align: right;
	font-size: 30px;
	box-sizing: border-box;
	display: none;
}

nav {
	width: 100%;
	background-color: #55834f;
	background-color: var(--navbar-gray-background-color);
	box-shadow: 0px 5px 5px rgba(58, 54, 54, 0.753);
	box-shadow: 0px 5px 5px var(--navbar-shadow-color);
	position: absolute;
	top: 0;
	z-index: 1;
	text-align: right;
	display: flex;
	align-items: center;
}

nav a .logo-initial {
	width: 65px;
	height: auto;
	padding-left: 12.5%;
	padding-top: 5%;
	padding-bottom: 5%;
}

nav .ulNavbar {
	width: 100%;
	margin: 0 auto;
	padding-right: 2.5%;
}

nav .ulNavbar li {
	display: inline-block;
	list-style: none;
	padding: 18px;
	border-bottom: 4px solid transparent;
}

nav .ulNavbar li:hover {
	border-bottom: 4px solid #ffffff;
	border-bottom: 4px solid var(--font-white-color);
}

nav .ulNavbar li a {
	color: #ffffff;
	color: var(--font-white-color);
	text-decoration: none;
	padding: 18px;
}

.logo-nombre {
	color: #ffffff;
	color: var(--font-white-color);
	text-decoration: none;
	font-size: 28px;
	margin-left: 20px;
	margin-top: 10px;
}

.logo-nombre span {
	color: black;
}
.logo-nombre-toggle {
	color: #ffffff;
	color: var(--font-white-color);
	text-decoration: none;
	font-size: 28px;
	margin-left: 20px;
	margin-top: 10px;
}
.logo-nombre-toggle span {
	color: black;
}

/* Media queries */
@media screen and (max-width: 1023px) {
	.logo-nombre-toggle {
		display: block;
	}
}
@media screen and (max-width: 767px) {
	.logo-nombre {
		margin-left: 0;
	}
	.logo-nombre-toggle {
		display: none;
	}
	nav {
		width: 100%;
		display: inline;
		display: initial;
	}

	nav a .logo-initial {
		display: none;
	}

	.ulNavbar {
		width: 100%;
		display: none;
	}
	.ulNavbar--active {
		display: block;
	}

	.ulNavbar li {
		width: 100%;
		display: block;
		text-align: center;
		margin-left: -3.5%;
	}

	nav .ulNavbar li:hover {
		border-bottom: 4px solid transparent;
	}

	.toggle {
		width: 100%;
		display: inline-block;
	}

	.logo-toggle {
		width: 50px;
		height: auto;
		padding: 0;
		margin: 0;
	}

	.burger-button {
		cursor: pointer;
	}

	.menu {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.active {
		display: block;
	}
}

