.team-distribution {
  background-color: #e4e4e4;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 12rem);
  grid-template-rows: repeat(4, 12rem);
  justify-items: center;
  justify-content: center;
  align-content: center;
  grid-column-gap: 25px;
  grid-row-gap: 7px;
  padding-top: 100px;
  padding-bottom: 20px;
}

.team-distribution button {
  display: none;
  outline: none;
}

.team-distribution img {
  width: 98%;
  height: 98%;
  border-radius: 50%;
  grid-row: 1/2;
  grid-column: 1/2;
}

.team-distribution-img {
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.5);
}

.team-distribution .team-distribution-img:hover button {
  font-family: "Play", sans-serif;
  color: #ffffff;
  text-shadow: 2px 2px 2px black;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  outline: none;
}

.team-distribution .img-1 {
  grid-column: 2/3;
  grid-row: 1/2;
}

.team-distribution .img-2 {
  grid-column: 3/4;
  grid-row: 1/2;
  height: auto;
}

.team-distribution .img-3 {
  grid-column: 4/5;
  grid-row: 2/3;
}

.team-distribution .img-4 {
  grid-column: 4/5;
  grid-row: 3/4;
}

.team-distribution .img-5 {
  grid-column: 3/4;
  grid-row: 4/5;
}

.team-distribution .img-6 {
  grid-column: 2/3;
  grid-row: 4/5;
}

.team-distribution .img-7 {
  grid-column: 1/2;
  grid-row: 3/4;
}

.team-distribution .img-8 {
  grid-column: 1/2;
  grid-row: 2/3;
}

.team-distribution .img-9 {
  grid-column: 2/4;
  grid-row: 2/4;
  width: 80%;
  height: 80%;
  justify-self: center;
  align-self: center;
  box-shadow: none;
}

.team-distribution .img-9 img {
  border: none;
  border-radius: 0%;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .team-distribution {
    grid-column-gap: 0;
    grid-template-columns: repeat(4, 10rem);
    grid-template-rows: repeat(4, 10rem);
  }
}
@media screen and (min-width: 320px) and (max-width: 767px) {
  .team-distribution {
    grid-template-columns: repeat(2, 9rem);
    grid-template-rows: repeat(5, 9rem);
    grid-column-gap: 10px;
    grid-row-gap: 7px;
    padding-top: 75px;
  }

  .team-distribution .img {
    grid-column: 1/3;
    grid-row: 1;
  }
  .team-distribution .img-1 {
    grid-column: 1/2;
    grid-row: 2;
  }

  .team-distribution .img-2 {
    grid-column: 2/3;
    grid-row: 2;
  }

  .team-distribution .img-3 {
    grid-column: 1/2;
    grid-row: 3;
  }

  .team-distribution .img-4 {
    grid-column: 2/3;
    grid-row: 3;
  }

  .team-distribution .img-5 {
    grid-column: 1/2;
    grid-row: 4;
  }

  .team-distribution .img-6 {
    grid-column: 2/3;
    grid-row: 4;
  }

  .team-distribution .img-7 {
    grid-column: 1/2;
    grid-row: 5;
  }

  .team-distribution .img-8 {
    grid-column: 2/3;
    grid-row: 5;
  }

  .team-distribution .img-9 {
    grid-column: 1/3;
    grid-row: 1;
  }

  .team-distribution .img-9 img {
    height: 150%;
    width: 75%;
  }

  .team-distribution button h2 {
    font-size: 20px;
  }
}

